				success() {
					console.log("成功");
				}
			})
		},
		opentake(){
			let that =this;
			let phone = uni.getStorageSync('phone');
			let user_uid = uni.getStorageSync('user_uid');
			let code = this.data.code;
			console.log("phone"+phone);
			if (phone) {
					uni.request({
						url:"https://applet.51tiaoyin.com/public/applet/work/take",
						method:"POST",
						dataType:JSON,
						data:{
							uid:user_uid,
							code:code,//订单编号
							WorkNautica:[this.longitude,this.latitude],
							phone:phone
						},
						success(res) {
							console.log(res);
							// console.log(res);
							const data = JSON.parse(res.data);
							console.log(data.code);
							if(data.code == 200){
								//抢单成功体醒
								
								//提醒订单
								that.show = true;
								that.isactive = true;
							}else if(data.code == 400){
								uni.showToast({
									title:"此订单已抢过!"
								});
								
								
							}else{
								uni.showToast({
									title:"服务器无响应"
								})
							}
						},
						fail() {
							console.log(res);
						}
					})
			} else {
				uni.showModal({
				    title: '提示',
				    content: '请先完成师傅服务类型注册，在抢单',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
							//如果没有手机说明用户没有注册跳转
							uni.navigateTo({
								url:"../settlement/settlement"
							})
				        } else if (res.cancel) {
				            console.log('用户点击取消');
							
				        }
				    }
				});
			}
		}
	},
